<script>
    import {link, location} from 'svelte-spa-router';

    let  showMenu = false;

    document.addEventListener('scroll', function(){
        let pageTop = document.documentElement.scrollTop;
        let pageBottom = pageTop + window.innerHeight;
        let tabs = document.querySelectorAll('.tab');

        for(let item of tabs){
            if(item.offsetTop < pageBottom){
                item.style.opacity = "1";
                item.style.transform = "translate(0, 0)";
            }
            else{
                item.style.opacity = "0";
                item.style.transform = "translate(0, 10vh)";
            }
        }

        let flag = document.querySelector('.home_world .container .title')
        let planets = document.querySelectorAll('.planet')
        for(let item of planets){
            if(flag.offsetTop < pageBottom){
                item.style.opacity = "1";
                item.style.transform = "translate(0, 0)";
            }
            else{
                item.style.opacity = "0";
                item.style.transform = "translate(0, 10vh)";
            }
        }
    })
</script>
<style lang="scss">
    @import './header.scss';
</style>

<div class="header">
    <div class="container">
        <div class="menu">
            <a href="/"><img src="./assets/logo/logo.png" alt="" class="logo"></a>
            <div class="menu-list" class:hide-menu={!showMenu}>
                <div class="hide-icon">
                    <img src="./assets/icon/hide-menu.svg" alt="" on:click={() => {showMenu = false}}>
                </div>
                <a href="/" class="menu-link" use:link class:menu-active={$location === '/'} on:click={() => {showMenu = false}}>
                    <img src="./assets/icon/menu6.svg" alt="" class="icon-inactive">
                    <img src="./assets/icon/menu1.svg" alt="" class="icon-active">
                    Home
                </a>
                <a href="/about-us" class="menu-link" use:link class:menu-active={$location === '/about-us'} on:click={() => {showMenu = false}}>
                    <img src="./assets/icon/menu7.svg" alt="" class="icon-inactive">
                    <img src="./assets/icon/menu2.svg" alt="" class="icon-active">
                    About Us
                </a>
                <a href="/products" class="menu-link" use:link class:menu-active={$location === '/products'} on:click={() => {showMenu = false}}>
                    <img src="./assets/icon/menu8.svg" alt="" class="icon-inactive">
                    <img src="./assets/icon/menu3.svg" alt="" class="icon-active">
                    Products
                </a>
                <a href="/career" class="menu-link" use:link class:menu-active={$location === '/career'} on:click={() => {showMenu = false}}>
                    <img src="./assets/icon/menu9.svg" alt="" class="icon-inactive">
                    <img src="./assets/icon/menu4.svg" alt="" class="icon-active">
                    Career
                </a>
                <a href="/contact" class="menu-link" use:link class:menu-active={$location === '/contact'} on:click={() => {showMenu = false}}>
                    <img src="./assets/icon/menu10.svg" alt="" class="icon-inactive">
                    <img src="./assets/icon/menu5.svg" alt="" class="icon-active">
                    Contact
                </a>
            </div>
        </div>
        <div>
            <img src="./assets/icon/menu.svg" alt="" class="menu-icon" on:click={() => {showMenu = true}}>
        </div>
        <div class="background" class:hide={!showMenu} on:click={() => {showMenu = false}}></div>    
    </div>
    
</div>